/* eslint-disable @typescript-eslint/consistent-type-definitions */
/* eslint-disable no-confusing-arrow */
import styled from 'styled-components';
import { BOTTOM_BAR_SIZE, ICON_BUTOTN_WIDTH } from '../constant';

export const NewLine = styled.div`
  height: 0;
  width: 100%;
`;

export const Divider = styled.div`
  width: 12px;
`;

export const AlignIcon = styled.div`
  flex: 1;
  height: 0;
  min-width: ${ICON_BUTOTN_WIDTH}px;
`;

export const GroupTitle = styled.div`
  width: 100%;
  margin-bottom: 8px;
  line-height: 18px;
  font-size: 12px;
  color: rgba(23, 26, 29, 0.4);
`;

type BoxProps = {
  isDisabled?: boolean;
  isActive?: boolean;
  isFocused: boolean;
  isStart?: boolean;
  isEnd?: boolean;
};

// 底部一级大按钮
const BoxBig = styled.div<BoxProps>`
  width: 32px;
  height: 32px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
  opacity: ${(props) => (props.isDisabled ? '0.24' : '1')};
  & > span {
    font-size: 24px;
  }
  & path {
    transition: fill 0.2s;
  }
  &::after {
    content: '';
    position: absolute;
    left: 4px;
    bottom: -3px;
    width: 24px;
    height: 3px;
    background: transparent;
    transition: background 0.2s;
    border-radius: 1.5px;
  }
  ${(props) =>
    props.isActive && !props.isFocused
      ? `
  & path {
    fill: #0089FF !important;
  }
  &::after {
    content: '';
    position: absolute;
    left: 4px;
    bottom: -3px;
    width: 24px;
    height: 3px;
    background: #0089FF;
    transition: background 0.2s;
    border-radius: 1.5px;
  }
  `
      : ''}
`;

// Panel 的上下图标结构。
const BoxIcon = styled.div<BoxProps>`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: ${ICON_BUTOTN_WIDTH}px;
  opacity: ${(props) => (props.isDisabled ? '0.5' : '1')};
  & > div:first-of-type {
    width: 44px;
    height: 44px;
    display: flex;
    justify-content: center;
    background: rgba(126, 134, 142, 0.12);
    align-items: center;
    border-radius: 6px;
    & > span {
      font-size: 24px;
    }
  }

  & > div:last-of-type {
    line-height: 20px;
    margin-top: 4px;
    color: #171a1d;
    font-size: 14px;
  }
`;

const BoxLine = styled.div<BoxProps>`
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 17px;
  color: #171a1d;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    width: calc(100% - 40px);
    height: 1px;
    background: rgba(126, 134, 142, 0.16);
  }

  & > :first-child {
    width: 24px;
    height: 24px;
    font-size: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 16px;
    & > span {
      font-size: 24px;
    }
  }
`;

// Panel 的按钮。加粗、斜体之类
const BoxHoriz = styled.div<BoxProps>`
  width: 100%;
  height: 100%;
  transition: background 0.2s;
  border-radius: ${(props) => {
    if (props.isStart && props.isEnd) {
      return '4px';
    }
    if (props.isStart) {
      return '4px 0 0 4px';
    }
    if (props.isEnd) {
      return '0 4px 4px 0';
    }
    return '0';
  }};
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: ${(props) =>
    props.isActive ? 'rgba(0, 137, 255, 0.12)' : 'rgba(126, 134, 142, 0.12)'};
  opacity: ${(props) => (props.isDisabled ? '0.5' : '1')};
  & > span {
    font-size: 20px;
  }
`;

type OuterProps = {
  isTouched: boolean;
  isActive: boolean;
  isFocused: boolean;
  flexWidth: number;
};

const OuterBig = styled.div<OuterProps>`
  cursor: pointer;
  height: ${BOTTOM_BAR_SIZE}px;
  width: ${BOTTOM_BAR_SIZE}px;
  min-width: ${BOTTOM_BAR_SIZE}px;
  display: flex;
  align-items: center;
  justify-content: center;
  ${(props) =>
    props.isTouched || props.isFocused
      ? `
  & ${BoxBig} {
   background: rgba(126, 134, 142, 0.16);
  }
  `
      : ''}
`;

const OuterIcon = styled.div<OuterProps>`
  flex: ${(props) => props.flexWidth};
  cursor: pointer;
  margin-right: 1px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  ${(props) =>
    props.isTouched
      ? `
  & ${BoxHoriz} {
   background: rgba(126, 134, 142, 0.24);
  }
  & ${BoxIcon} > div:first-of-type {
    background: rgba(126, 134, 142, 0.24);
  }
  `
      : ''}
`;

const OuterLine = styled.div<OuterProps>`
  cursor: pointer;
  width: 100%;
  height: 56px;
  padding-left: 16px;
  transition: background 0.2s;
  ${(props) =>
    props.isTouched
      ? `
    background: rgba(126, 134, 142, 0.12);
`
      : ''}
`;

const OuterHoriz = styled(OuterIcon)`
  height: 44px;
`;

export const Outers = {
  big: OuterBig,
  horiz: OuterHoriz,
  line: OuterLine,
  icon: OuterIcon,
};

export const Boxes = {
  big: BoxBig,
  horiz: BoxHoriz,
  line: BoxLine,
  icon: BoxIcon,
};

export const ImageIcon = styled.img`
  width: 24px;
  height: 24px;
`;
